.card {
  width: 200px;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 32px 16px 16px;
}

.card__photo {
  display: block;
  height: 134px ;
  width: 160px;
  margin: 0 3px 40px;
}

.card__name {
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  color: #060b35;
  margin-bottom: 4px;
}

.card__code {
  margin: 0;
  font-weight: 400;
  font-size: 10px;
  line-height: 14px;
  color: #616070;
  margin-bottom: 16px ;
}

.card__rating {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;

}

.card__reviews {
  margin: 0;
  color: #060b35;
  font-weight: 400;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.card__price-title {
  margin: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #616070;
}

.card__price-value {
  margin: 0;
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
  color: #060b35;
}

.card__button {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  width: 166px;
  height: 40px;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  background: #00acdc;
  border-radius: 5px;
  text-decoration: none;
  text-align: center;
}

.card__button:hover {
  background-color: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
  color: #00acdc;
}
